/* Admin Menu Toggle : Start */
.wb-responsive-menu {
	margin: 0;
	height: 50px;
	display: none;
	color: #1d2327;
	cursor: pointer;
	padding: 0 20px;
	font-size: 15px;
	overflow: hidden;
	line-height: 50px;
	font-weight: 500;
}

.wb-toggle-icon .wb-icon-bars {
	width: 25px;
	height: 3px;
	display: none;
	position: relative;
	background: #1d2327;
	transition: background .2s ease-out;
}

.wb-toggle-icon .wb-icon-bars:before,
.wb-toggle-icon .wb-icon-bars:after {
	background: #1d2327;
	content: '';
	display: block;
	height: 100%;
	position: absolute;
	width: 100%;
	transition-duration: 0.3s, 0.3s;
	transition-delay: 0.3s, 0s;
}

.wb-toggle-icon .wb-icon-bars:before {
	top: -8px;
	transition-property: top, transform;
}

.wb-toggle-icon .wb-icon-bars:after {
	bottom: -8px;
	transition-property: bottom, transform;
}

.wb-toggle-btn {
	display: none !important;
}

.wb-toggle-btn:checked~.wb-toggle-icon .wb-icon-bars {
	background: transparent;
}

.wb-toggle-btn:checked~.wb-toggle-icon .wb-icon-bars:before {
	top: 0;
	position: relative;
	-webkit-transform: rotate(-45deg);
	-ms-transform: rotate(-45deg);
	transform: rotate(-45deg);
}

.wb-toggle-btn:checked~.wb-toggle-icon .wb-icon-bars:after {
	bottom: 0;
	-webkit-transform: rotate(45deg);
	-ms-transform: rotate(45deg);
	transform: rotate(45deg);
}

.wb-toggle-btn:checked~.wb-toggle-icon .wb-icon-bars:before,
.wb-toggle-btn:checked~.wb-toggle-icon .wb-icon-bars:after {
	transition-delay: 0s, 0.3s;
}

.wb-toggle-icon {
	float: right;
	display: none;
	cursor: pointer;
	padding: 10px 2px;
	margin: 14px 0 0 0;
	position: relative;
	-webkit-tap-highlight-color: transparent;
	-webkit-tap-highlight-color: transparent;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.wb-toggle-icon .wb-icon-bars {
	width: 25px;
	height: 3px;
	display: none;
	position: relative;
	background: #1d2327;
	transition: background .2s ease-out;
}

@media screen and (max-width: 1189px) {
